.btn {
      border-radius: 5px;
      outline: none;
      padding: 0.3rem 1rem;
      box-shadow: var(--box-shadow-primary);
      border: 1px solid var(--color-deep-blue);
}

.btn:hover,
.btn:active {
      position: relative;
      cursor: pointer;
      font-style: 700;
      transform: translateY(0px);
      transition: all 0.4s ease;
      background-color: var(--color-green)
}

.btn-action:hover {
      box-shadow: 0 2px 7px 0.5px rgba(var(--color-dark-1), 0.8);
      transform: translateY(-5px);
      background-color: var(--color-green)
}

.btn-action {
      background-color: var(--color-deep-blue);
}
.btn-action-1{
      background-color: var(--color-green);
}
.btn-action-2{
      background-color: rgba(var(--color-purple-1), 0.3);
}

.icon {
      translate: translateY(0rem) scale(1);
      transition: all 0.2s;
      color: inherit;
      font-size: 1.2rem;
      position: relative;
}

.icon:hover,
i:active {
      color: var(--color-purple);
      translate: translateY(-1rem) scale(1.2);

}
