.profile-page{
      margin-top: 5rem;
}


.profile_radio input:not(:checked) + label + div {
      display: none;
}

.profile_radio input:checked+div {
      display: flex;
}

.image, img {
      width: 100px;
      height: 100px;
}
table img{
      width: 20px;
      height: 20px;
}
.failure{
      border: 1px solid red;
      color: red;
}
.success{
      color: green;
      border: green;
}